/* stylelint-disable */
.advance-button {
  position: absolute;
  bottom: 30px;
  right: 10px;
  width: 280px;
  cursor: pointer;
  z-index: 1;
  animation: fade-in 1s ease-in-out;

  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}

.advance-button-1 {
  fill: #00ff98;
}

.advance-button-2 {
  fill: #fff;
}

.advance-button-3 {
  fill: #17263c;
}
