/* stylelint-disable */
.info-box {
  left: 13px;
  position: absolute;
  top: 0;
  width: 400px;
  z-index: 20000;

  &.wide {
    width: 600px;
  }

  &.animate {
    animation-name: infobox-appear;
    animation-duration: 0.5s;
  }

  &.hidden {
    opacity: 0;
    animation: none;
  }
}

.info-box-arrow {
  left: 0;
  position: absolute;
  top: 0%;
  width: 100%;
  height: 100%;
  &::before {
    content: '';
    display: block;
    background-color: white;
    position: absolute;
    top: calc(50% + 1vw);
    left: 10px;
    transform: rotate(135deg);
    background: linear-gradient(to bottom right, transparent 50%, #fff 0%) top left, transparent;
    background-color: white;
    position: absolute;
    box-shadow: 0px 1px 11px 0px rgba(0, 0, 0, 0.64);
    width: 20px;
    height: 20px;
    top: calc(50% - 10px);
    left: -10px;
    box-sizing: border-box;
  }
  &.left {
    &::before {
      left: calc(100% - 10px);
      top: calc(50% - 10px);
    }
  }
  &.bottom {
    &::before {
      left: calc(50% - 10px);
      top: calc(0% - 10px);
      background-color: #0078d4;
      background: linear-gradient(to bottom right, #0078d4 50%, #0078d4 0%) top left, transparent;
    }
  }
  &.top {
    &::before {
      left: calc(50% - 10px);
      top: calc(100% - 10px);
    }
  }
}

.info-box-content-main {
  z-index: 1;
  background: white;
  position: inherit;
  border-radius: 10px;
}

.infobox-arrow {
  position: absolute;
}

.info-box-content {
  background: #fff;
  border-radius: 10px;
  box-shadow: 1px 1px 5px;
  min-height: 100px;
}

.info-box-content-title {
  background: #0078d4;
  border-radius: 10px 10px 0 0;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 15px 15px 15px 19px;
}

.close-icon {
  width: 30px;
  cursor: pointer;
  path {
    fill: #fff;
  }
}

.info-box-content-close {
  background-repeat: no-repeat;
  position: absolute;
  height: 30px;
  right: 10px;
  top: 12px;
  width: 30px;

  &:hover {
    background-repeat: no-repeat;
    cursor: pointer;
  }
}

.info-title {
  font-size: 16px;
  font-weight: 600;
  color: #fff;
}

.info-details-container {
  margin: 12px 20px 20px 20px;
}

.info-details {
  font-size: 16px;
  color: #000;
  line-height: 1.5;
}

.info-box-content-footer {
  display: flex;
  padding: 0 20px 20px 20px;
}

.info-part {
  font-size: 16px;
  color: #737373;
  flex: auto;
  font-style: italic;
  margin-top: 5px;
}

.info-box-next {
  font-size: 12px;
  font-weight: 600;
  background-color: #000;
  border: solid 0 #000;
  border-radius: 0 0 0 1px;
  color: #fff;
  cursor: pointer;
  line-height: 2;
  text-align: center;
  white-space: nowrap;
  width: 96px;
}

.info-box-next:hover {
  background-color: #737373;
  border: solid 0 #000;
}

.next-text {
  padding: 3px;
}

.relative-wrap {
  width: 100%;
  height: 100%;
  position: relative;
}

@keyframes infobox-appear {
  0% {
    opacity: 0;
    transform: translateX(5%);
  }

  75% {
    opacity: 0;
    transform: translateX(5%);
  }

  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}
